import './index.html';
import './styles/index.scss';
import { petsInfo } from './pets';
import { userInfo } from './user'

let windowWidth = window.innerWidth;
window.addEventListener('resize', ()=> {
    windowWidth = window.innerWidth;
    if (windowWidth === 1000 || windowWidth === 1600 || windowWidth === 640 || windowWidth === 320) {
        const testimonials = document.querySelector('.testimonials-container');
        testimonials.remove();
        addTestimonials();
        scroll.value = 0; //на случай, если до этого скролл был в другмо положении
    }
});

const burgerMenuBtn = document.querySelector('.burger-menu-btn');
const burgerMenu = document.querySelector('.burger-menu');

function openBurgerMenu() {
    burgerMenu.classList.remove('burger-menu--inactive');
} 
burgerMenuBtn.addEventListener('click', openBurgerMenu);

const closeBtn = document.querySelector('.close-menu-btn');
const popUpBg = document.querySelector('.popup__background');
function closeBurgerMenu(event) {
    if (event.target === closeBtn || 
        event.target === popUpBg ||
        event.target.innerText === 'About'|| 
        event.type === 'scroll') burgerMenu.classList.add('burger-menu--inactive');    
}
window.addEventListener('click', closeBurgerMenu);
window.addEventListener('scroll', closeBurgerMenu);

const rightArrow = document.querySelector('.right');
const leftArrow = document.querySelector('.left');

function shuffle(array) {
    for (let i = array.length - 1; i > 0; i--) {
        let j = Math.floor(Math.random() * (i + 1));
        let temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }
}
shuffle(petsInfo);
shuffle(userInfo);

function addSlides() {
    //создаем слайды при загрузке страницы
    if (windowWidth > 320) {
        const slider = document.querySelector('.slider');
        const slideOne = createSlide();
        const slideTwo = createSlide();
        slider.append(slideOne);
        slider.prepend(slideTwo);
    }
}
window.addEventListener('load', addSlides);

function addTestimonials() {
    //создаем отзывы при загрузке страницы
        const testimonials = document.querySelector('.testimonials');
        const newTestimonials = createTestimonials();
        testimonials.children[0].after(newTestimonials);
}
window.addEventListener('load', addTestimonials)

function moveCards(event) {
    rightArrow.removeEventListener('click', moveCards);
    leftArrow.removeEventListener('click', moveCards);

    const slides = document.querySelectorAll('.slide');
    const slideOne = slides[0];
    const slideTwo = slides[1];
    const slideThree = slides[2];
    const slideWidth = slideOne.getBoundingClientRect().width;
    const gapWidth = `${slideWidth + 220}`;
    const newSlide = createSlide();

    slideTwo.classList.add('animation');
    if (event.target === rightArrow || event.target === rightArrow.children[0]) {
        slideOne.classList.add('animation'); 
        slideOne.style.left = `${gapWidth}px`;
        slideTwo.style.left = `${gapWidth}px`;
        setTimeout(() => {
            slideOne.classList.remove('animation'); 
            slideOne.before(newSlide);
            slideOne.removeAttribute('style');
            slideThree.remove();
            rightArrow.addEventListener('click', moveCards);
            leftArrow.addEventListener('click', moveCards);
            newSlide.replaceWith(createSlide());
            slideTwo.replaceWith(createSlide());
       }, 1000) 
    } else if (event.target === leftArrow || event.target === leftArrow.children[0]) {
        slideThree.classList.add('animation');
        slideTwo.style.left = `${-gapWidth}px`;
        slideThree.style.left = `${-gapWidth}px`;
        setTimeout(() => {
            slideThree.classList.remove('animation');
            slideThree.after(newSlide);
            slideThree.removeAttribute('style');
            slideOne.remove();
            rightArrow.addEventListener('click', moveCards);
            leftArrow.addEventListener('click', moveCards);
            newSlide.replaceWith(createSlide());
            slideTwo.replaceWith(createSlide());
        }, 1000)
    }
}
rightArrow.addEventListener('click', moveCards);
leftArrow.addEventListener('click', moveCards);

function createSlide() {
    const slide = document.createElement('div');
    slide.classList.add('slide');
    if (windowWidth >= 1000) {
        for (let i = 0; i < 6; i++) {
            const newCard = createCard();
            slide.append(newCard);
        } 
    } else if (windowWidth >= 640 && windowWidth < 1000) {
        for (let i = 0; i < 4; i++) {
            const newCard = createCard();
            slide.append(newCard);
        } 
    }
    shuffle(petsInfo);
    return slide;
}
function createCard() {
    const card = document.createElement('div');
    const photo = new Image();
    photo.src = `${petsInfo[0].photo}`;
    card.append(photo);

    const caption = document.createElement('div');
    caption.classList.add('caption');
    card.append(caption);

    const captionText = document.createElement('div');
    captionText.classList.add('caption__text');
    caption.append(captionText);

    const petName = document.createElement('h3');
    petName.innerText = `${petsInfo[0].name}`;
    captionText.append(petName);

    const place = document.createElement('p');
    place.innerText = `${petsInfo[0].place}`;
    captionText.append(place);

    const food = new Image();
    food.src = `${petsInfo[0].food}`;
    caption.append(food);

    const element = petsInfo.shift();
    petsInfo.push(element);
    
    return card;  
}

function createTestimonial() {
    const testimonial = document.createElement('div');
    testimonial.classList.add('testimonials__card');
    //создаем информацию о юзере
    const user = document.createElement('div');
    user.classList.add('user');
    const userPhoto = new Image();
    userPhoto.src = `${userInfo[0].photo}`;
    userPhoto.classList.add('user-photo');
    user.append(userPhoto);

    const userInformation = document.createElement('div');
    userInformation.classList.add('user__information');

    const name = document.createElement('p');
    name.innerText = `${userInfo[0].name}`;
    userInformation.append(name);

    const from = document.createElement('span');
    from.innerText = `${userInfo[0].from}`;
    userInformation.append(from);

    const dot = document.createElement('span');
    dot.innerHTML = '&#183;';
    userInformation.append(dot);

    const time = document.createElement('span');
    time.innerText = `${userInfo[0].time}`;
    userInformation.append(time);
    
    user.append(userInformation);
    testimonial.append(user)
    
    //создаем текст отзыва
    const text = document.createElement('p');
    text.innerHTML = `${userInfo[0].text}`;
    testimonial.append(text);

    const element = userInfo.shift();
    userInfo.push(element);
    
    return testimonial;
}

function createTestimonials() {
    const testimonials = document.createElement('div');
    testimonials.classList.add('testimonials-container');
    if (windowWidth > 1000) {
        for (let i = 0; i < 11; i++) {
            const testimonial = createTestimonial();
            testimonials.append(testimonial);
        }
    } else if (windowWidth === 1000) {
        for (let i = 0; i < 10; i++) {
            const testimonial = createTestimonial();
            testimonials.append(testimonial);
        }
    } else {
        for (let i = 0; i < 3; i++) {
            const testimonial = createTestimonial();
            testimonial.addEventListener('click', (e) => {
                //создаем поп-ап отзыва
                const testimonial = e.target;
                const cloneTestimonial = testimonial.cloneNode(true);
                const testimonialsSection = document.querySelector('.testimonials');
                testimonialsSection.before(cloneTestimonial);
                cloneTestimonial.classList.remove('testimonials__card')
                cloneTestimonial.classList.add('testimonials-card--big');
                const close = new Image();
                close.src = 'assets/active_menu.svg';
                close.classList.add('close-btn');
                cloneTestimonial.prepend(close);
                
                //создаем бэкграунд
                const body = document.body;
                const popUpBg = document.createElement('div');
                popUpBg.classList.add('testimonials-bg');
                body.prepend(popUpBg);
                popUpBg.addEventListener('click', () => {
                    cloneTestimonial.remove();
                    popUpBg.remove();
                })
                window.addEventListener('scroll', () => {
                    cloneTestimonial.remove();
                    popUpBg.remove();
                })
                close.addEventListener('click', () => {
                    cloneTestimonial.remove();
                    popUpBg.remove();
                })  
            });
            testimonials.append(testimonial);
        }
    }
    testimonials.addEventListener('wheel', wheelTestimonials);
    /* testimonials.addEventListener('wheel', function(event) {
        window.clearTimeout(isScrolling);
        isScrolling = setTimeout(() => {
            wheelTestimonials(event);
        }, 150)
    }); */
    
    return testimonials;
}

const scroll = document.querySelector('.scroll');
let previousValue = scroll.value;
scroll.addEventListener('click', () => {
    const currentValue = scroll.value;
    const valueDiff = currentValue - previousValue;
    const testimonials = document.querySelector('.testimonials-container');
    let scrollStep;
    if (windowWidth > 1000) scrollStep = 298;
    else scrollStep = 323;
    testimonials.scrollLeft += parseInt(`${scrollStep * valueDiff}`);
    /* console.log(testimonials.getBoundingClientRect()) */
    previousValue = currentValue; 
})

let isScrolling;
function wheelTestimonials(event) {
    event.preventDefault();
    const testimonials = document.querySelector('.testimonials-container');
        if (event.deltaY > 0) {
            scroll.value++;
            scroll.click();
        } else {
            scroll.value--;
            scroll.click();
        }
}



